.contact-us {

  &__info {
    margin: 0 0 24px;

    &--last {
      margin-bottom: 32px;
    }
  }

  &__info-title {
    margin: 0 0 8px;
    font-size: 14px;
    line-height: 27px;
    color: #9fadc2;
  }

  &__info-link {
    color: inherit;
    text-decoration: none;

    transition: color 0.1s, font-size 0.3s;

    &:hover {
      font-size: 17px;
      color: rgb(0, 0, 255);
    }
  }

  &__field {
    margin: 0 0 16px;
  }

  &__field--last {
    margin-bottom: 32px;
  }
}
